import React, { FC } from 'react'
import clsx from 'clsx'
import { StepsProps } from './types'
import './Steps.scss'
import { getActiveClass } from './helpers'

const Sticker: FC<StepsProps> = ({
  className,
  currentStep,
  countOfSteps,
  sequenced = true,
}) => {
  const steps = new Array(countOfSteps).fill(1)

  return (
    <div className={clsx(className, 'StepsNext')}>
      {steps.map((_, index) => (
        <div
          key={index}
          className={clsx(
            'StepsNext__Step',
            getActiveClass(index + 1, currentStep, sequenced),
          )}
        />
      ))}
    </div>
  )
}

export default Sticker
